.body{
  color: #2c2c2c !important;
}
.pad-top-xs{
  padding-top: 10px !important;
}
.pad-xs{
  padding: 10px !important;
}
.mar-top-xs{
  margin-top: 10px !important;
}
.mar-top-sm{
  margin-top: 10px !important;
}
.mar-top-md{
  margin-top: 20px !important;
}
.mar-bottom-md{
  margin-top: 20px !important;
}
.mar-top-lg{
  margin-top: 30px !important;
}
.no-mar{
  margin: 0 !important;
}
.icon-color{
  color:black;
}
.no-decoration{
  text-decoration: none;
}
a {
  text-decoration: none;
  background-color: transparent;
}
a:hover {
  text-decoration: none !important;
}
.organization-widget-pad{
  padding: 2px !important;
  padding-left: 0 !important;
  margin-left: 0 !important;
}
.organization-widget-pad > .col-md-2{
  padding: 0;
  margin: 1px;
  text-align: center;
}
.nav-color{
  background-color:#24292e !important;
}
.rep-card{
  padding-left: 0px !important;
  padding-right: 0px !important;
}
.rep-card > .card{
  padding: 10px;
  margin:5px;
}
#rep_card{
  height: 175px;
}
.card-icon{
  vertical-align: text-bottom;
}

.card-title{
  margin-left: 5px;
  font-size: 1.2rem;
  font-weight: 600;
}
.rep-card .app-card-footer{
  position: absolute;
    left: 0;
    bottom: 0;
    padding: 10px;
    width: 100%;
}
.rep-card .rep-skills-container{
  overflow: hidden;
  float: left;
  display: inline-block;
  width: 70%;
  max-height: 30px;
  position: relative;
}
.rep-skills-container:after {
  content: "";
    text-align: right;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 30px;
    height: 30px;
    background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 50%);
}
.rep-card .app-card-footer >.rep-skills-container > div{
  margin-right: 10px;
  display: inline;
}
.rep-card .list-group-item{
  padding:0.75rem 1rem;
} 
.language-mark{
  width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-right: 5px;
    margin-left: 5px;
    display: inline-block;
}

.skill-page .list-group-item{
  padding:0.55rem 0.25rem;
}
.app-text-truncate{
  display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
.modal-50w{
  max-width: 50vw !important;
}